.nav {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index: 1;
    background-color: #111;


    /* Animations */
    transition-timing-function: ease-in;
    transition: 0.5s;
}

.nav__black{
    background-color: #111;
}

.nav__logo {
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.nav__avatar{
    position: fixed;
    width: 30px;
    right: 20px;
    object-fit: contain;
}